









































































































































































































.template {
  background-color: #fff;
  color: rgb(17, 17, 17);
  width: 100%;
  word-break: break-all;
  position: relative;
  min-height: 29.6cm;
  --cv-color: #5182c2;
}

.inner {
  padding: 30px;
}

.header {
  display: flex;
  height: 200px;
  background-color: var(--cv-color);
}

.img-box {
  width: 200px;
  height: 100%;
  flex-shrink: 0;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  padding-left: 40px;
  h1 {
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 0;
  }
}

.profession {
  font-size: 20px;
  font-weight: 500;
}

.address {
  font-size: 14px;
  font-weight: 500;
  margin-top: 30px;
  margin-bottom: 0;
}

.body {
  display: flex;
}

.col-left {
  padding-top: 20px;
  flex-shrink: 0;
}

.col-right {
  padding-top: 20px;
  padding-right: 20px;
  flex-grow: 1;
}

.title {
  font-size: 20px;
  font-weight: bold;
  &.small {
    font-size: 14px;
  }
}

.description {
  font-size: 11px;
}

.subtitle {
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  span {
    font-weight: 500;
    font-size: 11px;
  }
}

.rating {
  display: flex;
  span {
    display: block;
    width: 30px;
    height: 2px;
    margin-right: 3px;
    background-color: rgb(174, 174, 174);
    &.active {
      background-color: #000;
    }
  }
}

.skill-name {
  font-size: 11px;
  font-weight: 500;
  margin-bottom: 0;
}

